/* Aniamtion for attacks */
.animateAttack {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 3;
  width: 0px;
  height: 0px;
  border-left: 3px solid white;
  border-right: 3px solid white;
  border-bottom: 180px solid white;
  -webkit-transform: rotateZ(45deg);
      -ms-transform: rotate(45deg);
          transform: rotateZ(45deg);
  -webkit-animation: attack 0.5s cubic-bezier(0.17, 0.67, 0.68, 0.81);
          animation: attack 0.5s cubic-bezier(0.17, 0.67, 0.68, 0.81);
}

@-webkit-keyframes attack {
  0% {
    -webkit-transform: translate3d(50px, -50px, 0) rotateZ(45deg);
            transform: translate3d(50px, -50px, 0) rotateZ(45deg);
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(-20px, 20px, 0) rotateZ(45deg);
            transform: translate3d(-20px, 20px, 0) rotateZ(45deg);
    opacity: 0;
  }
}

@keyframes attack {
  0% {
    -webkit-transform: translate3d(50px, -50px, 0) rotateZ(45deg);
            transform: translate3d(50px, -50px, 0) rotateZ(45deg);
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(-20px, 20px, 0) rotateZ(45deg);
            transform: translate3d(-20px, 20px, 0) rotateZ(45deg);
    opacity: 0;
  }
}

/* Aniamtion for defend */
.animateDefend {
	/* background-color: blue; */
	background-image: url("../../Images/Icons/shield_icon_transp_small.png");
	background-size: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  /* z-index: -1; */
  margin: 0 auto;
  height: 100px;
  width: 100px;
  -webkit-animation: defend 1s cubic-bezier(0.88, 0.18, 0.99, 0.46);
          animation: defend 1s cubic-bezier(0.88, 0.18, 0.99, 0.46);
}

@-webkit-keyframes defend {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes defend {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* Aniamtion for fade */
.animateFade {
  -webkit-animation: fade 3s cubic-bezier(0, 0.71, 0.47, 0.35);
          animation: fade 3s cubic-bezier(0, 0.71, 0.47, 0.35);
}


@-webkit-keyframes fade {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


@keyframes fade {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

