/* regular buttons */

button {
	background: #225C5BD9;
	color: #F6F7EB;
	border: none;
	position: relative;
	margin-bottom: 10vh;
	height: 60px;
	font-size: 1.6em;
	padding: 0 2em;
	cursor: pointer;
	-webkit-transition: 800ms ease all;
	-o-transition: 800ms ease all;
	transition: 800ms ease all;
	outline: none;
}
button:hover {
	background: #ffffff00;
	color: #F6F7EB;
}
button:before, button:after {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	height: 2px;
	width: 0;
	background: #225C5B;
	-webkit-transition: 400ms ease all;
	-o-transition: 400ms ease all;
	transition: 400ms ease all;
}
button:after {
	right: inherit;
	top: inherit;
	left: 0;
	bottom: 0;
}
button:hover:before, button:hover:after {
	width: 100%;
	-webkit-transition: 800ms ease all;
	-o-transition: 800ms ease all;
	transition: 800ms ease all;
}


